*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
html{
    scroll-behavior: smooth;
}
div,section,header,span,h1,h2,h3,span,a,img,video,nav,label,input,form,picture,iframe{
    display:block;
    width:100%;
    margin:auto}

a{
    text-decoration:none;
    color: black;
}
iframe{
    border: 0;
}
body{
    font-family: 'Poppins', sans-serif;
    // margin-bottom: 50vh;
    background: #eee;
}
body.dark{
    background: #000;
    color: white;
    a{
        color: white;
    }
}
img{
    height:100%;
    object-fit:cover;
    object-position:center
}
.grid{display:grid}
.flex{display:flex;align-items:center}
.rel{position:relative}
.abs{position:absolute}
.tr5{transition:.5s}
.tr3{transition:.3s}
.ma9{width: 90%;}
.ma95{width: 95%;}
