.app_ha{
    margin-top: 20px;
    max-width: 1100px;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 15px;
    section{
        grid-template-columns: 70px 1fr;
        gap: 6px;
        height: 70px;
        background: rgba(221, 221, 221, 0.589);
        border-radius: 15px;
        overflow: hidden;
        padding: 10px;
        i{
            font-size: 47px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        span{
            h2{
                font-size: 18px;
            }
            h3{
                font-size: 13px;
                color: #888;
            }
        }
    }
}



body.dark{
    .app_ha{
        section{
            background: rgb(15, 15, 15);
            i{
                color: white;
            }
            span{
                h2{
                    color: white;
                }
                h3{
                    color: #aaa;
                }
            }
        }
    }
}



