.app_perfil{
    max-width: 1000px;
    margin-top: 100px;
    .per_h1{
        h1{
            font-size: 30px;
            text-transform: uppercase;
            position: relative;
            &::after{
                content: '';
                position: absolute;
                left: 0;
                bottom: -1px;
                background:#004cda;
                width: 100px;
                height: 4px;
            }
        }
    }
    .per_in{
        h3{
            color: #333;
            font-size: 15px;
            font-weight: 100;
            letter-spacing: 4px;
            margin: 20px auto 15px;
        }
        div{
            grid-template-columns: 200px 1fr;
            gap: 5px;
            picture{
                width: 100%;
                height: auto;
                // margin-top: 0;
                // border-radius: 50%;
                // box-shadow: 0 0 10px #aaa;
                img{
                    object-fit: cover;
                    object-position: center;
                }
            }
            section{
                h1{
                    color: #004cda;
                }
                h2{
                    font-size: 35px;
                    color: #111;
                }
                h3{
                    font-weight: 100;
                    letter-spacing: 2px;
                    margin: 0 auto 15px;
                }
                span{
                    p{
                        margin-bottom: 8px;
                        font-size: 14px;
                    }
                }
                .in_lin{
                    margin-left: 0;
                    justify-content: center;
                    width: 280px;
                    a{
                        width: 40px;
                        height: 40px;
                        padding: 5px;
                        background: #ddd;
                        border-radius: 10px;
                        // box-shadow: 0 0 3px #444;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        position: relative;
                        i{
                            font-size: 22px;
                        }
                        &::before,&::after{
                            display: block;
                            background: #ddd;
                            font-family: 'Poppins', sans-serif;
                            position: absolute;
                            text-align: center;
                            opacity: 0;
                            z-index: -1;
                            transition: .25s all ease-in-out;
                            content: attr(data-title);
                        }
                        &:hover::before,&:hover::after{
                            opacity: 1;
                        }
                        &::before{
                            content: '';
                            width: 15px;
                            height: 10px;
                            bottom: -7px;
                            left: calc(50% - 7.5px);
                            clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
                        }
                        &::after{
                            content: attr(data-title);
                            width: 80px;
                            bottom: 0;
                            left: -20px;
                            font-size: 13px;
                            border-radius: 6px;
                            // transform: translate();
                            // bottom: -28px;
                        }
                        &:hover::after{
                            bottom: -26px;
                        }
                    }
                }
            }
        }
    }
}


body.dark{
    .app_perfil{
        .per_h1{
            h1{
                color: #fff;
                &::after{
                    background:#ceaf00;
                }
            }
        }
        .per_in{
            h3{
                color: #ccc;
            }
            div{
                // picture{
                //     box-shadow: 0 0 10px #ffff008a;
                // }
                section{
                    h1{
                        color: #ceaf00;
                    }
                    h2{
                        color: #eee;
                    }
                    span{
                        color: white;
                    }
                    .in_lin{
                        a{
                            background: #222;
                            i{
                                color: white;
                            }
                            &::before,&::after{
                                background: #222;
                                color: white;
                            }
                        }
                    }
                }
            }
        }
    }
}



@media screen and (max-width:700px) {
    .app_perfil{
        text-align: center;
        .per_h1{
            h1::after{
                left: calc(50% - 50px);
            }
        }
        .per_in{
            div{
                display: block;
                margin-bottom: 15px;
                picture{
                    height: 350px;
                }
            }
            section{
                margin-top: 20px;
                width: 90%;
            }
        }
    }
}


