.app_pr{
    margin-top: 20px;
    margin-bottom: 50vh;
    max-width: 1000px;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 15px;
    div{
        margin-bottom: 20px;
        height: 180px;
        border-radius: 25px;
        overflow: hidden;
        box-shadow: 0 0 5px #888;
        // background: rgba(221, 221, 221, 0.589);
        picture{
            top: 0;
            left: 0;
            height: 100%;
        }
        > section{
            background: rgba(230, 230, 230, 0.8);
            backdrop-filter: blur(5px);
            height: 100%;
            justify-content: center;
            opacity: 0;
            section{
                text-align: center;
                h2,a{
                    opacity: 0;
                    transition: .5s all ease-in;
                }
                h2{
                    font-size: 20px;
                    letter-spacing: 2px;
                    transform: translateY(-25px);
                }
                span{
                    i{
                        margin-left: 5px;
                        font-size: 24px;
                    }
                    .html{color: #ff2600}
                    .css{color: #0067ce}
                    .js{color: #dbba00}
                    .php{color: #aa00aa;}
                    .ajax{color: #c70000;}
                    .db{color: #003bb9;}
                    .mov{color: #ff81cf;}
                }
                a{
                    margin-top: 5px;
                    width: 100px;
                    border: 1px solid black;
                    padding: 0px 0;
                    font-weight: 300;
                    border-radius: 10px;
                    transform: translateY(25px);
                }

            }
            &:hover{
                opacity: 1;
                section{
                    h2,a{
                        transform: translateY(0);
                        opacity: 1;
                    }
                }
            }

        }
    }
}
body.dark{
    .app_pr{
        div{
            >section{
                background: rgba(40, 40, 40, 0.589);
                section{
                    // span{
                    //     i{
                    //         background: #fff;
                    //     }
                    // }
                    a{
                        border-color: #fff;
                    }
                }
            }
        }
    }
}