.app_pre{
    height: 100vh;
    overflow: hidden;
    .pre_bol{
        width: 500px;
        height: 500px;
        background: blue;
        left: calc(50% - 250px);
        top: calc(50vh - 250px);
        // margin: 100px auto;
        // margin: 100px 40px 100px auto;
        // border-radius: 80px 80px 80px 80px;
        animation: linear infinite 4s radio;
        box-shadow: 0 0 100px #006eff, 0 0 70px #001e5e;
    }
    .pre_tex{
        height: 100vh;
        background: rgba(238, 238, 238, 0.65);
        backdrop-filter: blur(30px);
        justify-content: center;
        section{
            text-align: center;
            h2{
                font-size: 23px;
                font-weight: 900;
                line-height: 2px;
            }
            h1{
                font-size: 70px;
                font-weight: 900;
                text-transform: uppercase;
                letter-spacing: 2.5px;
                span{
                    display:unset;
                    color: #006eff;
                }
            }
            > span{
                h3{
                    font-size: 15px;
                    font-weight: 100;
                    letter-spacing: 3px;
                    color: #111;
                }
                span{
                    margin-top: 5px;
                    i{
                        font-size: 32px;
                        margin-left: 8px;
                    }
                }
            }
            a{
                margin-top: 40px;
                cursor: pointer;
                border-radius: 15px;
                font-size: 20px;
                background: #0a9dff;
                // box-shadow: 0 0 100px #0a9dff, 0 0 70px #001e5e;
                padding:10px 0;
                border: 0;
                color: white;
                width: 200px;
                letter-spacing: 2px;
                font-weight: 900;
                transition: .5s all ease-in-out;
                &:hover{
                    box-shadow: 0 0 30px 5px #0a9dff;
                }
            }
        }

    }
}
@keyframes radio {
    0%{   border-radius: 500px 400px 200px 180px; }
    20%{  border-radius: 100px 200px 450px 350px;  }
    40%{  border-radius: 400px 300px 100px 200px;  }
    60%{  border-radius: 200px 500px 300px 500px; }
    80%{  border-radius: 300px 100px 400px 300px; }
    100%{ border-radius: 500px 400px 200px 180px; }
}
