body.dark{
    .app_pre{
        .pre_bol{
            background: #ff0;
            box-shadow: 0 0 100px #ff0, 0 0 70px #ff0;
        }
        .pre_tex{
            background: rgba(0, 0, 0, 0.85);
            section{
                color: white;
                h1{
                    span{
                        color: #ff0;
                    }
                }
                > span{
                    h3{
                        color: #eee;
                    }
                }
                a{
                    background: #ff0;
                    color: black;
                    &:hover{
                        box-shadow: 0 0 30px 5px #ff0;
                    }
                }
            }
    
        }
    }
}


@media screen and (max-width:700px) {
    .app_pre{
        .pre_tex{
            section{
                h2{
                    font-size: 18px;
                }
                h1{
                    font-size: 40px;
                    letter-spacing: 2px;
                }
                a{
                    font-size: 16px;
                }
            }
    
        }
    }
}




