.titulo{
    width: 200px;
    text-align: center;
    padding-top: 200px;
    position: relative;
    color: #222;
    letter-spacing: 3px;
    text-transform: uppercase;
    &::after{
        position: absolute;
        bottom: -4px;
        left: 50px;
        content: '';
        background: blue;
        width: 100px;
        height: 5px;
    }
}
body.dark{
    .titulo{
        color: #eee;
        &::after{
            background: #ff0;
        }
    }
}

@media screen and (max-width:700px) {
    .titulo{
        font-size: 26px;
    }
}