.app_header{
    padding: 20px 0;
    position: fixed;
    z-index: 99;
    .app_nav{
        max-width: 1100px;
        display: flex;
        flex-direction: row;
        // color: #c70000  ;
        // h1{
        //     background: red;
        // }
        picture{
            width: 200px;
            height: auto;
            img{
                object-fit: contain;
            }
        }
        .app_ico{
            display: none;
        }
        section{
            width: 70%;
            max-width: 600px;
            flex-direction: row;
            justify-content: center;
            a{
                width: auto;
                font-size: 15px;
                position: relative;
                i{
                    font-size: 16px;
                    color: blue;
                    margin-left: 5px;
                }
                &::before{
                    content: '';
                    display: block;
                    width: 0%;
                    height: 1px;
                    position: absolute;
                    bottom: -3px;
                    left: -5%;
                    background: #2701ff;
                    box-shadow: 0 0 4px #2701ff;
                    transition: .4s all ease-in-out;
                }
                &:hover::before{
                    width: 110%;
                }
            }
            .mode{
                cursor: pointer;
                font-size: 27px;
            }
        }
    }
}
.app_header.scroll{
    background: rgba(238, 238, 238, 0.568);
    backdrop-filter: blur(13px);
}
body.dark{
    .app_header{
        // background: #111;
        .app_nav{
            h1{
                color: #eee;
            }
            section{
                a{
                    color: white;
                    i{
                        color: yellow;
                    }
                    &::before{
                        background: yellow;
                        box-shadow: 0 0 4px #ff0;
                    }
                }
                .mode{
                    color: yellow;
                }
            }
        }
    }
    .app_header.scroll{
        background: rgba(20, 20, 20, 0.568);
    }
}
@media screen and (max-width:700px) {
    .app_header{
        .app_nav{
            picture{
                position: relative;
                top: -10px;
                left: -50px;
                width: 170px;
                img{
                    object-fit: contain;
                }
            }
            .app_ico{
                top: 0;
                right: 0;
                width: 30px;
                height: 30px;
                // background: rgba(92, 92, 92, 0.671);
                // border-radius: 41% 59% 26% 74% / 73% 26% 74% 27%;
                // backdrop-filter: blur(10px);
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
                // z-index: 100;
                i,i::after,i::before{
                    background: #000;
                    position: absolute;
                    width: 100%;
                    height: 3px;
                    transition: .5s all ease-in-out;
                }
                i{
                    &::after,&::before{
                        content: '';
                        left: 0;
                    }
                    &::before{
                        top: 10px;
                    }
                    &::after{
                        bottom: 10px;
                    }
                }
            }
            section{
                position: absolute;
                width: 110%;
                left: -150%;
                top: 50px;
                height: 100vh;
                display: block;
                background: rgba(238, 238, 238, 1);
                a{
                    width: 90%;
                    font-size: 15px;
                    padding: 15px 0 5px;
                }
                .mode{
                    position: absolute;
                    left: calc(50% - 30px);
                    bottom: 250px;
                    cursor: pointer;
                    font-size: 35px;
                    width: 60px;
                    height: 60px;
                    box-shadow: 0 0 15px #888;
                    margin: 0 auto;
                    border-radius: 50%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
            }
        }
        .app_nav.men{
            .app_ico{
                i{
                    transform: rotate(.125turn);
                    &::after,&::before{
                        transform: rotate(.25turn);
                        // bottom: 0;
                        // top: 0;
                    }
                    &::before{
                        top: 0;
                    }
                    &::after{
                        bottom: 0;
                    }
                }
            }
            section{
                left: -5%;
            }
        }
    }
    .app_header.scroll{
        background: rgb(238, 238, 238);
    }
    body.dark{
        .app_header{
            .app_ico{
                i,i::after,i::before{
                    background: #fff;
                }
            }
            .app_nav{
                section{
                    background: #000;
                }
            }
        }
    }
}


